<template>
  <div>
    <h2 v-show="firstView">查询案例</h2>
      <div>
        <div>
          <h5> 例如输入地址为: 长春市宽城区上海路贵阳小区13号楼114号房  </h5>
          <h5> 返回如下结果：</h5>
          province: "吉林省" <br>
          city: "长春市" <br>
          distinct: "宽城区" <br>
          latitude: "43.998252407950645" <br>
          longitude: "125.34489933527382"  <br>
          address: "上海路贵阳小区13号楼114号房" <br>
        </div>
        <br>
        <br>
        <h2>分析结果如下：</h2> <br>
        <div>
          province: {{users.province}} <br>
          city: {{users.city}} <br>
          distinct: {{users.distinct}} <br>
          latitude: {{users.latitude}} <br>
          longitude: {{users.longitude}}  <br>
          address: {{users.address}} <br>
        </div>
      </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'

  export default {
    computed: {
      ...mapState(['firstView', 'loading', 'errorMsg', 'users'])
    }
  }
</script>

<style>
  .card {
    float: left;
    width: 33.333%;
    padding: .75rem;
    margin-bottom: 2rem;
    border: 1px solid #efefef;
    text-align: center;
  }

  .card > img {
    margin-bottom: .75rem;
    border-radius: 100px;
  }

  .card-text {
    font-size: 85%;
  }
</style>



