<template>
  <section class="jumbotron">
    <h2 class="jumbotron-heading">请输入地址查询</h2> <br>
    <div>
      <input type="text" placeholder="enter the address you search" v-model="searchName" style='width: 400px'/>
      <button @click="search">查询</button>
    </div>
    <h2 v-show="loading">请求中...</h2>
    <h2 v-show="errorMsg">{{errorMsg}}</h2>
  </section>
</template>

<script>
  export default {
    data () {
      return {
        searchName: ''
      }
    },

    methods: {
      search () {
        const searchName = this.searchName.trim()
        if(searchName) {
          this.$store.dispatch('search', searchName)
        }
      }
    }
  }
</script>

<style>

</style>



